<template>
    <el-container style="height: 500px; border: 1px solid #eee">
        <el-header class="el-icon-s-home" style=" font-size: 40px; background-color:#409EFF; color:azure">
            教考系统</el-header>
        <el-footer>
            <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect">
                <el-menu-item index="1"><router-link to="/teacher" tag="span">账号信息</router-link></el-menu-item>
                <el-menu-item index="2"><router-link to="/questionBank" tag="span">题库管理</router-link></el-menu-item>
                <el-menu-item index="3"><router-link to="/examPaper" tag="span">试卷管理</router-link></el-menu-item>
                <el-menu-item index="4"><router-link to="/examSchedule" tag="span">考试安排</router-link></el-menu-item>
                <el-menu-item index="4"><router-link to="/examGrading" tag="span">阅卷</router-link></el-menu-item>
                <el-menu-item index="4"><router-link to="/class" tag="span">班级管理</router-link></el-menu-item>
                <el-menu-item index="5"><router-link to="/studentLogin" tag="span">退出登录</router-link></el-menu-item>
            </el-menu>
        </el-footer>


        <el-main>

            <el-form :model="inputData" :inline="true" label-width="50px" style="width: 500px;"
                class="demo-form-inline">
                <el-form-item prop="key">
                    <el-input v-model="inputData" placeholder="请输入关键字以搜索"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="likeGet">查询</el-button>
                </el-form-item>
            </el-form>


            <el-table :data="tableData" style="width: 100%">
                <el-table-column prop="schemeNum" label="考试编号" width="200">
                </el-table-column>
                <el-table-column prop="schemeBegin" label="开始时间" width="200">
                </el-table-column>
                <el-table-column prop="schemeEnd" label="结束时间" width="200">
                </el-table-column>
                <el-table-column prop="examNum" label="试卷编号" width="200">
                </el-table-column>
                <el-table-column prop="course.courseName" label="考试课程" width="200">
                </el-table-column>
                <el-table-column prop="examType.typeName" label="考试类型" width="200">
                </el-table-column>
                <el-table-column prop="classNum" label="参与班级" width="200">
                </el-table-column>
                <el-table-column label="操作" width="200">
                    <template slot-scope="scope">
                        <el-button type="text" @click="gradingExam(scope.row.schemeNum)">查看</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </el-main>
    </el-container>

</template>

<script>
import axios from 'axios';
export default {
    data() {
        return {
            tableData: [],
            classData: [],
            inputData: '',
            dialogTableVisible: false,
            dialogFormVisible: false,
            dialogFormVisible2: false,
            formLabelWidth: '120px',
            form: {
                schemeNum: '',
                schemeBegin: '',
                schemeEnd: '',
                classNum: '',
                teacherNum: '',
                examNum: '',
                course: {
                    courseName: '',
                },
                examType: {
                    typeNum: '',
                    typeName: '',
                }
            },
        }
    },
    methods: {
        handleClick(row) {
            console.log(row);
        },
        onSubmit() {
            console.log('submit!');
        },
        //跳转阅卷页面
        gradingExam(examID) {
            this.$router.push({ name: 'grading', params: { id:Number(examID) } });
        },
        //查询全部
        getAll() {
            axios.get("/api/examScheme").then((result) => {
                this.tableData = result.data.data;
            });
        },
        //模糊查询
        likeGet() {
            axios.get(`/api/examScheme/inputData/${this.inputData}`).then((result) => {
                this.tableData = result.data.data;
            });
        },
        //成功提示
        open2() {
            this.$message({
                message: '恭喜你，这是一条成功消息',
                type: 'success'
            });
        },
        //错误提示
        open3() {
            this.$message({
                message: '警告哦，这是一条警告消息',
                type: 'warning'
            });
        },
    },
    mounted() {
        this.getAll();
    }
}
</script>

<style scoped>
.el-container {
    min-height: 100vh;
    /* 至少占据整个视口高度 */
    max-height: 1200px;
    /* 最大高度限制 */
    overflow-y: auto;
    /* 如果内容超出高度，显示滚动条 */
    display: flex;
    flex-direction: column;
}

.el-header,
.el-footer {
    flex: 0 0 auto;
    /* 固定高度 */
    background-color: #409EFF;
    color: azure;
    padding: 10px;
}

.el-main {
    flex: 1 1 auto;
    /* 自动占据剩余空间 */
    overflow-y: auto;
    /* 如果内容超出高度，显示滚动条 */
    padding: 20px;
    box-sizing: border-box;
}
</style>